import React from 'react';
import HeaderContainer from '../../container/HeaderContainer';
import Footer from '../common/Footer/index';
import SideMenu from '../common/SideMenu/index';
import { FindBox } from './styles';


const Find = ({ param, onChange, findInfo }) => {
    const { username, email, userId } = findInfo;

    return (
        <FindBox>
            <HeaderContainer />
            <SideMenu find />
            <section className="findForm">
                <div className="find_tit">
                    {param === "find_id" ? " " : " "}
                </div>
                <div className="form_box">
                    <form className="write_form">
                        <div className="form_tit"></div>
                        <input onChange={onChange} value={username} name="username" placeholder="  " />
                        {
                            param === "find_pwd" &&
                            (
                                <>
                                    <div className="form_tit upper"></div>
                                    <input onChange={onChange} value={userId} name="userId" placeholder="  " />
                                </>
                            )
                        }
                        <div className="form_tit upper"></div>
                        <input onChange={onChange} value={email} name="email" placeholder="     " />
                        <div className=" btnBox">
                            <a href>
                                <span className="loginBtn">
                                    {param === "find_id" ? "" : ""}
                                </span>
                            </a>
                        </div>
                    </form>
                </div>
            </section>
            <Footer />
        </FindBox>
    );
}


export default Find;